// progress bar animation
$(".level5.latest").delay(800).animate({
  width: "20%"
}, 200, 'linear');
$(".level4.latest").delay(600).animate({
  width: "20%"
}, 200, 'linear');
$(".level3.latest").delay(400).animate({
  width: "20%"
}, 200, 'linear');
$(".level2.latest").delay(200).animate({
  width: "20%"
}, 200, 'linear');
$(".level1.latest").delay(0).animate({
  width: "20%"
}, 200, 'linear');
$(".level0.latest").animate({
  width: "0%"
}, 0, 'linear');

$(".level5.done").animate({
  width: "80%"
}, 800, 'linear');
$(".level4.done").animate({
  width: "60%"
}, 600, 'linear');
$(".level3.done").animate({
  width: "40%"
}, 400, 'linear');
$(".level2.done").animate({
  width: "20%"
}, 200, 'linear');
$(".level1.done").animate({
  width: "0%"
}, 0, 'linear');
$(".level0.done").animate({
  width: "0%"
}, 0, 'linear');

// Insert Icon for tracking-status-icon
$(".tracking-status-icon.exclamation").append('<i class="far fa-exclamation-triangle double-flash"></i>');
$(".tracking-status-icon.check").append('<i class="far fa-check-circle"></i>');
