$(function () {
  $("#faqInput").on("keyup", function () {
    var value = $(this).val().toLowerCase();
    $("#faqContent li").filter(function () {
      $(this).toggle($(this).text().toLowerCase().indexOf(value) > -1);
      // var faqResults = $('#faqContent li:visible').length;
      // console.log(faqResults);
    });
  });
});