// Foundation JS relies on a global variable. In ES6, all imports are hoisted
// to the top of the file so if we used `import` to import Foundation,
// it would execute earlier than we have assigned the global variable.
// This is why we have to use CommonJS require() here since it doesn't
// have the hoisting behavior.

// import $ from 'jquery';
window.$ = $;
window.jQuery = $;

require('foundation-sites');

// If you want to pick and choose which modules to include, comment out the above and uncomment
// the line below
//import './lib/foundation-explicit-pieces';

import 'what-input';
import './lib/timeline';

import libs from './lib/dependancies';
window.libs = libs;

require('./lib/settingsCookieAlert');
require('./lib/backToTop');
require('./lib/accordionCloseSmall');
require('./lib/accordionExpandAll');
require('./lib/faqFilter');
require('./lib/magellanIds');
require('./lib/navEscClear');
require('./lib/navFlip');
require('./lib/homeResizeHero');
require('./lib/settingsCookiePickup');
require('./lib/formAnalysis');
require('./lib/accordionShowMore');
require('./lib/timeit');
require('jplist-es6/dist/1.2.0/jplist.min');
require('./lib/searchFocus');
require('./lib/trackingFocus');
require('./lib/trackingStyles');
require('./lib/formFunctions');
require('./lib/footerYear');
require('./lib/homeMoreScroll');

$(document).foundation();

jplist.init();

if ('serviceWorker' in navigator) {
  window.addEventListener('load', () => {
   navigator.serviceWorker.register('../../sw.js').then( () => {
    console.log('Service Worker Registered')
   })
 })
}
