// Set Id on index page menu
var magellanId = 1;
$('.magellan > li a').each(function () {
  $(this).attr('href', '#' + magellanId);
  magellanId++;
});

// Sets Id on section
var magellanSection = 1;
$('.section > section').each(function () {
  $(this).attr({
    'data-magellan-target': magellanSection,
    'id': magellanSection,
  });
  magellanSection++;
});